

import React, { useEffect, useState } from 'react'
import { withRedux } from '../../store/redux'
import { useSelector, useDispatch } from 'react-redux'


import { withRouter, NavLink} from 'react-router-dom';

import {  OpenWatchDialog} from '../../actions/common'

import * as _Util from '../../store/Util'

import '../_styles.css'

import loadable from '@loadable/component'

const RowViewPort = loadable(() => import('../rowList'))

const RowItemViewPort = loadable(() => import('../rowViewPortItem'))

const Icon2 = loadable(() => import('../Icons'))



const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges);
  const dispatch = useDispatch(); 

  const _openMd = (_id, item) => {
    let data = {};
    data['zIndex']=450;
    data['Id']=_id;
    data['observeResize']=true;    
    data['props']={item:item}; 

    OpenWatchDialog(dispatch,data);
  }
  



  return { 
    observeChanges,
    _openMd
  }
}




  














const BrowseComponent = (props) => {
  const {
    _openMd
  } = useObserveChanges();

  let _state = _Util.getStore();
  let keys = _Util.getGlobalsKeys()
  _state["keys"] = keys;
 

  const [initialize, setInitialize] = useState(false);

  const [widthScreen, setWidthScreen] = useState(null);
   
  /*
  const searchHash = window.location.search.split('?')[1]?window.location.search.split('?')[1]:null;
 
  const router = _Util.parseQuery(searchHash);
  */

 const typeBrowse = window.location.hash.split('/')[1];

  let outerWidth = _state["outerWidth"];


  const openSetting = (e) => {    
    // _openMd(id, {running:e})
  }



  useEffect(() => {  
    if(widthScreen!==outerWidth){
      setWidthScreen(outerWidth);
    } 
    if(!initialize){
      setInitialize(true);
      setTimeout(()=>window.scrollTo(0,0),350);
    }
  });
  

  let _latestMovies = _state["userList"];

  // _mlt = _Util.sortObjectsByKey(_state["userList"],"episode","desc");

    return (
      <>
        <div className={`main2View browseList`} > 
        <div className={` _dsplFlx `}>             
              <div className={`flxbsc70`}/>
              <div className={`flxbsc10`}>
                <NavLink  to={{pathname:"/blocks"}} className="">
                  <p className={`_Id_card `}>
                    <Icon2 name={'dashboard'}/>
                  </p>
                </NavLink>
              </div>        
              <div className={`flxbsc10`}/>     
              <div className={`flxbsc10`}>
                <p className={`_Id_card `} onClick={()=>openSetting()}>
                    <Icon2 name={'filter'}/>
                </p>
              </div>
            </div>       
          <div className={`lolomo padd30`}>
            <RowViewPort  
              data={_latestMovies} 
              keyCode={_state.keys[66]}  
              typeBrowse={typeBrowse} 
              title={_Util.translatetext(525)}
              _key_={7} 
              _ID={7}
              Child={RowItemViewPort}
            />
          </div>
        </div>
      </>
    );
  
}  





export default withRouter(withRedux(BrowseComponent))
